import { HourglassBottom } from "@mui/icons-material";
import {
  Box,
  Button,
  Card,
  CardContent,
  Dialog,
  DialogContent,
  DialogTitle,
  Divider,
  Grid,
  Rating,
  Typography,
} from "@mui/material";
import { useEffect, useState } from "react";
import color from "../components/color";
import { CustomTextField } from "../components/style";
import { getUserId } from "../services/axiosClient";
import { getAllMyBookings } from "../services/services";

const ReviewPopup = ({ open, handleClose, selectedCard, onSubmit }: any) => {
  const [rating, setRating] = useState<number>(selectedCard?.rating || 0);
  const [review, setReview] = useState("");

  const handleSubmit = () => {
    onSubmit(selectedCard.id, rating);
    handleClose();
  };

  return (
    <Dialog open={open} onClose={handleClose}>
      <DialogTitle>Review - {selectedCard?.title}</DialogTitle>
      <DialogContent>
        <Box display="flex" flexDirection="column" gap={2}>
          <Typography>Rate your experience:</Typography>
          <Rating
            value={rating}
            onChange={(e, newValue) => setRating(newValue || 0)}
          />
          <CustomTextField
            sx={{
              "& .MuiInputBase-input": { resize: "vertical" },
              "& textarea": { resize: "vertical" },
              "& .MuiOutlinedInput-root": {
                "& fieldset": {
                  border: "none",
                  borderRadius: "12px",
                },
              },
            }}
            label="Write your review"
            multiline
            rows={4}
            fullWidth
            value={review}
            onChange={(e) => setReview(e.target.value)}
          />
          <Button
            sx={{
              background: "transparent",
              border: "solid 2px",
              color: color.firstColor,
              borderColor: color.firstColor,
              borderRadius: "44px",
              textTransform: "none",
            }}
            variant="contained"
            onClick={handleSubmit}
          >
            Submit Review
          </Button>
        </Box>
      </DialogContent>
    </Dialog>
  );
};

const MyBookings = () => {
  const [data, setData] = useState<any[]>([]);
  const [open, setOpen] = useState(false);
  const [selectedCard, setSelectedCard] = useState<any | null>(null);
  const formatCurrency = (amount: number | string) => {
    if (!amount) return "₹0.00";

    let num = Number(amount);

    // Convert paise → rupees when number is too large
    if (num > 1000) {
      num = num / 100;
    }

    if (isNaN(num)) return amount;

    return num.toLocaleString("en-IN", {
      style: "currency",
      currency: "INR",
      minimumFractionDigits: 2,
    });
  };



  useEffect(() => {
    const Payload = {
      data: { filter: "", userId: getUserId() },
      page: 0,
      pageSize: 50,
      order: [["createdAt", "ASC"]],
    };
    getAllMyBookings(Payload).then((res) => {
      setData(res?.data?.data?.rows || []);
    });
  }, []);

  // Helper: try to build a JS Date from common fields.
  // Priorities:
  // 1. card.checkInDateTime (iso string)
  // 2. combine card.checkInDate + card.checkInTime
  // If parsing fails returns null
  const parseBookingDate = (card: any): Date | null => {
    try {
      // 1) explicit combined field
      if (card?.checkInDateTime) {
        const d = new Date(card.checkInDateTime);
        if (!isNaN(d.getTime())) return d;
      }

      // 2) try if date is ISO already
      if (card?.checkInDate) {
        // normalize weird apostrophes (eg: “ ’ ”) and trim
        const datePart = String(card.checkInDate).replace(/[\u2018\u2019\u201C\u201D]/g, "'").trim();
        const timePart = card?.checkInTime ? String(card.checkInTime).trim() : "";

        // try common combos
        const attempts = [
          // If datePart looks like ISO or 'YYYY-MM-DD'
          `${datePart} ${timePart}`,
          `${datePart}T${timePart}`,
          // fallback: try Date constructor on datePart only
          datePart,
        ];

        for (const s of attempts) {
          if (!s) continue;
          const d = new Date(s);
          if (!isNaN(d.getTime())) return d;
        }

        // Last resort: try to parse human readable like "28 Dec 2016 10:30"
        const cleaned = `${datePart.replace(/’|‘|’/g, "")} ${timePart}`.replace(/[\u2018\u2019\u201C\u201D]/g, "");
        const d2 = new Date(cleaned);
        if (!isNaN(d2.getTime())) return d2;
      }
      return null;
    } catch (e) {
      return null;
    }
  };

  // Returns true if there is MORE than 24 hours left before booking start
  const isCancelable = (card: any) => {
    const bookingDate = parseBookingDate(card);
    if (!bookingDate) return false; // can't parse -> disallow
    const now = new Date();
    const diffMs = bookingDate.getTime() - now.getTime();
    const oneDayMs = 24 * 60 * 60 * 1000;
    return diffMs > oneDayMs;
  };

  const handleOpen = (card: any) => {
    setSelectedCard(card);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setSelectedCard(null);
  };

  const handleSubmitReview = (id: number, rating: number) => {
    setData((prevData) =>
      prevData.map((card) => (card.id === id ? { ...card, rating } : card))
    );
  };

  // Cancel handler - optimistic UI update. Replace the comment with your cancel API call.
  const handleCancelBooking = async (card: any) => {
    // confirm again on client if needed (optional)
    // if (!isCancelable(card)) return;

    // Optimistic update: set status to 'cancelled' locally
    setData((prev) =>
      prev.map((c) => (c.id === card.id ? { ...c, status: "cancelled" } : c))
    );

    try {
      // TODO: call your cancel booking API here.
      // Example:
      // await cancelBookingApi({ bookingId: card.id, userId: getUserId() });
      //
      // On success: you may refresh or keep optimistic update.
      //
      // On failure: rollback the optimistic update and show error.
    } catch (error) {
      // rollback
      setData((prev) =>
        prev.map((c) => (c.id === card.id ? { ...c, status: card.status } : c))
      );
      // show error to user (toast/snackbar) - not implemented here
      console.error("Failed to cancel booking", error);
    }
  };

  return (
    <Box
      sx={{
        background: color.thirdColor,
        px: { xs: 2, md: 4 },
        py: 4,
      }}
    >
      <Typography
        variant="h4"
        fontWeight="bold"
        sx={{
          textAlign: "center",
          color: color.firstColor,
          mb: 2,
        }}
      >
        Your Bookings
      </Typography>

      <Grid container spacing={2}>
        {data.map((card, index) => {
          const cancelEnabled = isCancelable(card);
          return (
            <Grid item xs={12} md={6} key={card.id ?? index}>
              <Card
                sx={{
                  margin: "auto",
                  mt: 2,
                  padding: 3,
                  boxShadow: "0px 0px 20px rgba(0, 0, 0, 0.18)",
                  borderRadius: "12px",
                  border: "solid 1px",
                  borderColor: color.firstColor,
                  position: "relative",
                }}
              >
                <CardContent style={{ padding: 0 }}>
                  <Box
                    sx={{
                      position: "absolute",
                      top: { xs: 5, md: 0 },
                      right: { xs: 25, md: 0 },
                      background:
                        card.status === "checked in" || card.status === "checked out"
                          ? "Green"
                          : card.status === "pending"
                            ? "#faaf00"
                            : "Red",
                      color: "white",
                      px: 1,
                      borderRadius: { xs: "4px", md: "0 6px 0 6px" },
                      py: 0.5,
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                      gap: "6px",
                      fontSize: "12px",
                    }}
                  >
                    {card.status}{" "}
                  </Box>

                  <Typography variant="h6">{card?.hotelName}</Typography>
                  <Typography color="textSecondary">
                    {card?.checkInDate}, {card?.checkInTime}, {card?.geustName}
                  </Typography>
                  <Typography>
                    {card?.adults} Adults, {card?.children} Child
                  </Typography>
                  <Typography variant="h6" sx={{ marginTop: 1 }}>
                    {formatCurrency(card?.amountPaid)}

                  </Typography>

                  <Divider
                    sx={{
                      mt: 1,
                      borderStyle: "dashed",
                      borderColor: "grey",
                      borderWidth: "1px",
                    }}
                  />

                  {card.status === "checked out" && (
                    <Box display="flex" alignItems="center" gap={1} mt={2}>
                      <Button
                        sx={{
                          background: "transparent",
                          border: "solid 2px",
                          color: color.firstColor,
                          borderColor: color.firstColor,
                          borderRadius: "44px",
                          textTransform: "none",
                        }}
                        onClick={() => handleOpen(card)}
                      >
                        Add Review
                      </Button>
                      <Rating value={card.rating} readOnly />
                    </Box>
                  )}

                  {card.status === "pending" && (
                    <Box display="flex" alignItems="flex-start" mt={2} color={"#faaf00"}>
                      <HourglassBottom />
                      <Box sx={{ ml: 1, display: "flex", alignItems: "center", gap: 1 }}>
                        <Typography>Upcoming</Typography>

                        {/* Cancel button for pending/upcoming bookings.
                            Enabled only when booking is > 24 hours away.
                            If booking is within 24 hours, button is disabled. */}
                        <Button
                          sx={{
                            background: "transparent",
                            border: "solid 2px",
                            color: color.firstColor,
                            borderColor: color.firstColor,
                            borderRadius: "44px",
                            textTransform: "none",
                            ml: 2,
                          }}
                          variant="contained"
                          onClick={() => handleCancelBooking(card)}
                          disabled={!cancelEnabled}
                        >
                          Cancel
                        </Button>
                      </Box>
                    </Box>
                  )}

                  {/* If status is 'cancelled' show a small label */}
                  {card.status === "cancelled" && (
                    <Box mt={2} color="red">
                      <Typography>Booking Cancelled</Typography>
                    </Box>
                  )}
                </CardContent>
              </Card>
            </Grid>
          );
        })}
      </Grid>

      {selectedCard && (
        <ReviewPopup
          open={open}
          handleClose={handleClose}
          selectedCard={selectedCard}
          onSubmit={handleSubmitReview}
        />
      )}
    </Box>
  );
};

export default MyBookings;
